body {
  background-image: url("background.png");
  font-family: 'Noto Sans JP', sans-serif; }

.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-bottom: 100px 
}

.container .btn-upload {
  width: 200px;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 12px;
  border-radius: 5px;
  background: #cbb9eb;
  font-family: sans-serif;
  color: #353535;
  font-size: 24px;
  text-align: center; }

.container .preview-box {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 600px;
  height: calc(240px);
  border: 1px solid #e0e0e0;
  background-color: #fafafa; }

.container .preview-content {
  width: auto;
  height: inherit; }

.heading {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: #ffffff; }

input {
  color: white;
  font-size: 1.4rem;
  background-color: #a775fd; }

.image_results {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  }

.image {
    width: 240px;
}
  